<template>
  <div class="login">
    <div class="form">
      <h1>登录页面</h1>
      <el-form
        label-position="left"
        label-width="60px"
        :model="signin"
        :rules="rules"
        status-icon
        ref="ruleform"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="signin.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="signin.password"></el-input>
        </el-form-item>
        <el-button type="primary" @click="submit">登录</el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    const validateUsername = (rule, value, callback) => {
      if (/^[0-9]/.test(value)) {
        callback(new Error("首位不能是数字"));
      } else {
        if (/^[^0~9].{1,11}$/.test(value)) {
          callback();
        } else {
          callback(new Error("账号长度6-12位"));
        }
      }
    };
    const validatePassWord = (rule, value, callback) => {
      if (/^[3-9]/.test(value)) {
        callback(new Error("首位不能是数字"));
      } else {
        if (/^[^3~9].{4,11}$/.test(value)) {
          callback();
        } else {
          callback(new Error("密码长度6-12位"));
        }
      }
    };
    const validateText = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请输入验证码"));
      }
      if (value.length != 4) {
        callback(new Error("请输入四位数字验证码"));
      } else {
        callback();
      }
    };

    return {
      signin: {
        username: "qqqwww",
        password: "111111",
      },
      rules: {
        username: [{ validator: validateUsername, trigger: "blur" }],
        password: [{ validator: validatePassWord, trigger: "blur" }],
      },
    };
  },
  watch: {
    "signin.text": {
      handler(value) {
        this.signin.text = value.toLocaleUpperCase();
      },
    },
  },
  methods: {
    submit() {
      this.$refs["ruleform"].validate((valid) => {
        if (valid) {
          this.$http.post("/login", {
            username: this.signin.username,
            password: this.signin.password
          }).then((res) => {
            console.log(res);
            if (res.data.code==200) {
              this.$message({
                type: "success",
                message: "登录成功",
              });
              //this.$cookie.set("class_id",res.data.token)
              this.$router.push("/");
              console.log(res.data);
            } else if(res.data.code == -3) {
              this.$message({
                type: "warning",
                message: "用户名不存在",
              });
            }else{
              this.$message({
                type: "error",
                message: "登录失败",
              });
            }
          });
        }
      });
    },
  },
};
</script>

<style scoped>
.login {
    display: flex;
    overflow: hidden;
}
.form {
  width: 450px;
  background-color: #ffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  /* background-color: rgba(255,255,255,60%); */
  box-shadow: 0 0 1rem 0.2rem rgb(0 0 0 / 10%);
  padding:0 50px;
}
.form h1{
  /* color: rgb(144,129,241); */
  text-align: center;
  margin-bottom: 50px;
}
.checking-code {
  height: 40px;
  cursor: pointer;
}
.el-button {
  width: 100%;
  /* background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%); */
  margin-bottom:40px;
}
.el-form-item{
padding-bottom:25px;
}
</style>

<style >
.checking {
  display: flex;
  align-items: center;
}
</style>